//
// Typography
// --------------------------------------------------

// Font-face generator

.font-face(@fontFamily, @fileName, @style, @weight) {
  @font-face {
    font-family: @fontFamily;
    font-style: @style;
    font-weight: @weight;
    src: url('@{fileName}.eot');
    src: url('@{fileName}.eot?#iefix') format('embedded-opentype'),
         url('@{fileName}.woff') format('woff'),
         url('@{fileName}.ttf') format('truetype'),
         url('@{fileName}.svg') format('svg');
    text-rendering: optimizeLegibility;
  }
}

.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-ExtraLight', normal, 200);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-ExtraLightIt', italic, 200);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-Light', normal, 300);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-LightIt', italic, 300);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-Regular', normal, 400);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-It', italic, 400);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-Semibold', normal, 600);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-SemiboldIt', italic, 600);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-Bold', normal, 700);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-BoldIt', italic, 700);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-Black', normal, 900);
.font-face('sourcesanspro', '../fonts/sourcesanspro/SourceSansPro-BlackIt', italic, 900);

.font-face('titillium', '../fonts/titillium/titillium-thin', normal, 200);
.font-face('titillium', '../fonts/titillium/titillium-thinitalic', italic, 200);
.font-face('titillium', '../fonts/titillium/titillium-light', normal, 300);
.font-face('titillium', '../fonts/titillium/titillium-lightitalic', italic, 300);
.font-face('titillium', '../fonts/titillium/titillium-regular', normal, 400);
.font-face('titillium', '../fonts/titillium/titillium-regularitalic', italic, 400);
.font-face('titillium', '../fonts/titillium/titillium-semibold', normal, 600);
.font-face('titillium', '../fonts/titillium/titillium-semibolditalic', italic, 600);
.font-face('titillium', '../fonts/titillium/titillium-bold', normal, 700);
.font-face('titillium', '../fonts/titillium/titillium-bolditalic', italic, 700);
.font-face('titillium', '../fonts/titillium/titillium-black', normal, 900);

.font-face('fenixedu-icons', '../fonts/fenixedu-icons/fenixedu-icons', normal, normal);
.font-face('glyphicons', '../fonts/glyphicons/glyphicons-halflings-regular', normal, normal);


// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: @headings-font-family;
  //font-weight: @headings-font-weight;
  line-height: @headings-line-height;
  color: @headings-color;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: @headings-small-color;
  }
}

h1, .h1,
h2, .h2,
h3, .h3 {
  margin-top: @line-height-computed;
  margin-bottom: (@line-height-computed / 2);

  small,
  .small {
    font-size: 65%;
  }
}
h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: (@line-height-computed / 2);
  margin-bottom: (@line-height-computed / 2);

  small,
  .small {
    font-size: 75%;
  }
}

h1, .h1 { font-size: @font-size-h1; font-style: normal; font-weight: 300; }
h2, .h2 { font-size: @font-size-h2; font-style: normal; font-weight: 300; }
h3, .h3 { font-size: @font-size-h3; font-style: normal; font-weight: 400; }
h4, .h4 { font-size: @font-size-h4; font-style: normal; font-weight: 400; }
h5, .h5 { font-size: @font-size-h5; font-style: normal; font-weight: 700; -webkit-font-smoothing: antialiased;  -moz-osx-font-smoothing: grayscale;}
h6, .h6 { font-size: @font-size-h6; font-style: normal; font-weight: 400; }


// Body text
// -------------------------

p {
  margin: 0 0 (@line-height-computed / 2);
}

.lead {
  margin-bottom: @line-height-computed;
  font-size: floor((@font-size-base * 1.15));
  font-weight: 300;
  line-height: 1.4;

  @media (min-width: @screen-sm-min) {
    font-size: (@font-size-base * 1.5);
  }
}


// Emphasis & misc
// -------------------------

// Ex: (12px small font / 14px base font) * 100% = about 85%
small,
.small {
  font-size: floor((100% * @font-size-small / @font-size-base));
}

// Undo browser default styling
cite {
  font-style: normal;
}

mark,
.mark {
  background-color: lighten(@brand-warning, 42.5%);
  padding: .2em .4em;
}

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

// Contextual colors
.text-muted {
  color: @text-muted;
}
.text-primary {
  .text-emphasis-variant(@brand-primary);
}
.text-success {
  .text-emphasis-variant(@state-success-text);
}
.text-info {
  .text-emphasis-variant(@state-info-text);
}
.text-warning {
  .text-emphasis-variant(@state-warning-text);
}
.text-danger {
  .text-emphasis-variant(@state-danger-text);
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary {
  // Given the contrast here, this is the only class to have its color inverted
  // automatically.
  color: #fff;
  .bg-variant(@brand-primary);
}
.bg-success {
  .bg-variant(@state-success-bg);
}
.bg-info {
  .bg-variant(@state-info-bg);
}
.bg-warning {
  .bg-variant(@state-warning-bg);
}
.bg-danger {
  .bg-variant(@state-danger-bg);
}


// Page header
// -------------------------

.page-header {
  padding-bottom: ((@line-height-computed / 2) - 1);
  margin: (@line-height-computed * 2) 0 @line-height-computed;
  border-bottom: 1px solid @page-header-border-color;
}


// Lists
// -------------------------

// Unordered and Ordered lists
ul,
ol {
  margin-top: 0;
  margin-bottom: (@line-height-computed / 2);
  ul,
  ol {
    margin-bottom: 0;
    margin-left: 18px;
  }
}

// List options

// Unstyled keeps list items block level, just removes default browser padding and list-style
.list-unstyled {
  padding-left: 0;
  list-style: none;
}

// Inline turns list items into inline-block
.list-inline {
  .list-unstyled();
  margin-left: -5px;

  > li {
    display: inline-block;
    padding-left: 8px;
    padding-right: 8px;
  }
}

// Description Lists
dl {
  margin-top: 0; // Remove browser default
  margin-bottom: @line-height-computed;
}
dt,
dd {
  line-height: @line-height-base;
}
dt {
  color: @gray-dark;
  margin-top: 8px;
}
dd {
  margin-left: 0; // Undo browser default
}

// Horizontal description lists
//
// Defaults to being stacked without any of the below styles applied, until the
// grid breakpoint is reached (default of ~768px).

.dl-horizontal {
  dd {
    &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
  }

  @media (min-width: @grid-float-breakpoint) {
    dt {
      float: left;
      width: (@dl-horizontal-offset - 20);
      clear: left;
      text-align: right;
      margin-top: 0;
      .text-overflow();
    }
    dd {
      margin-left: @dl-horizontal-offset;
    }
  }
}


// Misc
// -------------------------

// Abbreviations and acronyms
abbr[title],
// Add data-* attribute to help out our tooltip plugin, per https://github.com/twbs/bootstrap/issues/5257
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted @abbr-border-color;
}
.initialism {
  font-size: 90%;
  text-transform: uppercase;
}

// Blockquotes
blockquote {
  display: inline-block;
  //padding: (@line-height-computed / 2) @line-height-computed;
  padding: (@line-height-computed * 1.5) 0;
  margin: 0 0 @line-height-computed;
  font-size: @blockquote-font-size;
  font-style: italic;
  border-top: 1px solid @blockquote-border-color;
  border-bottom: 1px solid @blockquote-border-color;

  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }

  // Note: Deprecated small and .small as of v3.1.0
  // Context: https://github.com/twbs/bootstrap/issues/11660
  footer,
  small,
  .small {
    display: block;
    margin-top: (@line-height-computed);
    font-size: 90%; // back to default font-size
    line-height: @line-height-base;
    color: @brand-primary;

    &:before {
      //content: '\2014 \00A0'; // em dash, nbsp
    }

    cite {
      color: @blockquote-small-color;
      font-style: inherit;
      display: block;
    }
  }
}

// Opposite alignment of blockquote
//
// Heads up: `blockquote.pull-right` has been deprecated as of v3.1.0.
.blockquote-reverse,
blockquote.pull-right {
  border-top: 1px solid @blockquote-border-color;
  border-bottom: 1px solid @blockquote-border-color;
  border-left: 0;
  text-align: right;

  // Account for citation
  footer,
  small,
  .small {
    &:before { content: ''; }
    &:after {
      //content: '\00A0 \2014'; // nbsp, em dash
    }
  }
}

// Quotes
blockquote:before,
blockquote:after {
  content: "";
}

// Addresses
address {
  margin-bottom: @line-height-computed;
  font-style: normal;
  line-height: @line-height-base;
}
